.tryagain {
  background-image: url("./assets/undraw_winners_ao2o\ 2.svg");
  max-width: 464px;
  height: 629px;
  background-repeat: no-repeat;
  margin: auto;
  position: relative;
}

.tryagain button {
  position: absolute;
  bottom: 20%;
  right: 2rem;
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  color: #1D355D;
  background: #FFFFFF;
  border-radius: 24px;
  padding-block: 1rem;
  padding-inline: 3.5rem;
}

.tryagain button:hover,
.next:hover {
  background-color: #F9A826;
  cursor: pointer;
}